// react imports
import React, { Component } from 'react';
// other modules imports
import { HashRouter, NavLink, Route, Switch } from 'react-router-dom';
import { createStore, applyMiddleware } from "redux";
import { Provider } from "react-redux";
import thunk from "redux-thunk";

// css, less or images imports
import './index.css';
import './business-manage-src/style.less';

// components or miscellaneous imports
import reducers from './business-manage-src/reducers';
import CertificationManage from './business-manage-src/CertificationManage';
import AccountManage from './business-manage-src/AccountManage';

// let or const definitions
let store = createStore(reducers, applyMiddleware(thunk));

// app entity
class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      routeRenderList: [
        { text: '', to: '/', path: '/', component: CertificationManage },
        { text: '', to: '/product', path: '/product', component: () => (<div>product</div>) },
        { text: '', to: '/order', path: '/order', component: () => (<div>order</div>) },
        { text: '', to: '/account', path: '/account', component: AccountManage },
      ],
      sideBarHeaderTitle: ''
    }
  }
  render() {
    let { routeRenderList, sideBarHeaderTitle } = this.state;
    return (
      <Provider store={store}>
        <HashRouter>
          <div className='app-container'>
            <div className="side-bar">
              <div className="side-bar-header">
                {sideBarHeaderTitle}
              </div>
              <ul className="menu-item-list">
                {
                  routeRenderList.map((item, index) => (
                    <li key={`menu-item-li-${index}`}>
                      <NavLink
                        to={item.to}
                        exact={item.to === '/'}
                        className='menu-item'
                        activeClassName='active-menu-item'
                      >{item.text}</NavLink>
                    </li>
                  ))
                }
              </ul>
            </div>
            <div className="content">
              <Switch>
              {
                routeRenderList.map((item, index) => (
                  <Route
                    key={`route-${index}`}
                    path={item.path}
                    exact={item.path === '/'}
                    component={item.component}
                  />
                ))
              }
              </Switch>
            </div>
          </div>
        </HashRouter>
      </Provider>
    );
  }
}

export default App;



